"use client";

import { useState } from "react";

export default function MyAppointmentsPage() {
  const [showDialog, setShowDialog] = useState(false);

  const handleReschedule = () => {
    setShowDialog(true);
  };

  const handleCloseDialog = () => {
    setShowDialog(false);
  };

  const handleConfirmReschedule = () => {
    setShowDialog(false);
    window.location.href = "/book-appointment"; // or use router.push if using next/navigation
  };

  return (
    <div className="max-w-5xl mx-auto">
      <h2 className="text-3xl font-bold mb-6 text-gray-800">My Appointments</h2>

      <table className="w-full bg-white rounded-lg shadow overflow-hidden">
        <thead className="bg-blue-600 text-white">
          <tr>
            <th className="p-3 text-left">Date</th>
            <th className="p-3 text-left">Doctor</th>
            <th className="p-3 text-left">Department</th>
            <th className="p-3 text-left">Status</th>
            <th className="p-3 text-left">Action</th>
          </tr>
        </thead>
        <tbody>
          <tr className="border-t">
            <td className="p-3">2025-04-20 10:00AM</td>
            <td className="p-3">Dr. Sarah Miller</td>
            <td className="p-3">Dermatology</td>
            <td className="p-3">Confirmed</td>
            <td className="p-3">
              <button
                onClick={handleReschedule}
                className="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700 transition"
              >
                Reschedule
              </button>
            </td>
          </tr>

          {/* You can duplicate a row for dummy data if needed */}
        </tbody>
      </table>

      {/* Dialog */}
      {showDialog && (
        <>
          <div className="fixed inset-0 bg-black bg-opacity-30 flex items-center justify-center z-50">
            <div className="bg-white p-6 rounded-lg shadow-lg max-w-sm w-full space-y-5">
              <h3 className="text-xl font-semibold text-gray-800">
                Do you want to reschedule your appointment?
              </h3>
              <div className="flex justify-end space-x-4">
                <button
                  onClick={handleCloseDialog}
                  className="px-4 py-2 border border-gray-300 rounded hover:bg-gray-100"
                >
                  No
                </button>
                <button
                  onClick={handleConfirmReschedule}
                  className="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700"
                >
                  Yes
                </button>
              </div>
            </div>
          </div>
        </>
      )}
    </div>
  );
}
